'use client'
import { getBrands, getCategory, getProductByCat } from '@/helpers/core/CoreFuncs'
import { useParams } from 'next/navigation'
import React from 'react'
import Image from 'next/image'
import Link from 'next/link'

export default function Page() {
  const { products } = useParams()
  const [category, setCategory] = React.useState({})
  const [productsInCategory, setProductsInCategory] = React.useState([])
  const [brands, setBrands] = React.useState([])
  const [filteredProducts, setFilteredProducts] = React.useState([])
  const [searchTerm, setSearchTerm] = React.useState('')
  const [selectedBrand, setSelectedBrand] = React.useState('')
  const [sortBy, setSortBy] = React.useState('name')
  const [currentPage, setCurrentPage] = React.useState(1)
  const [priceRange, setPriceRange] = React.useState({ min: 0, max: 10000 })
  const [inStockOnly, setInStockOnly] = React.useState(false)
  const productsPerPage = 6

  React.useEffect(() => {
    const fetchData = async () => {
      try {
        const prodsInCat = await getProductByCat(products)
        const catSingle = await getCategory(products)
        const blands = await getBrands()

        if (prodsInCat && prodsInCat.PsByCat) {
          setProductsInCategory(prodsInCat.PsByCat)
          setFilteredProducts(prodsInCat.PsByCat)
          setCategory(catSingle.category)
          setBrands(blands.brands)
        }
      } catch (error) {
        console.log('Error fetching products', error)
      }
    }

    fetchData()
  }, [products])

  // Apply all filters and search
  React.useEffect(() => {
    let result = [...productsInCategory]

    // Search filter
    if (searchTerm) {
      result = result.filter(product => 
        product.name.toLowerCase().includes(searchTerm.toLowerCase()) || 
        product.description.toLowerCase().includes(searchTerm.toLowerCase())
      )
    }

    // Brand filter
    if (selectedBrand) {
      result = result.filter(product => product.brand === selectedBrand)
    }

    // Price range filter
    result = result.filter(product => 
      product.price >= priceRange.min && product.price <= priceRange.max
    )

    // In stock filter
    if (inStockOnly) {
      result = result.filter(product => product.inStock)
    }

    // Sort
    result.sort((a, b) => {
      switch(sortBy) {
        case 'price-asc':
          return a.price - b.price
        case 'price-desc':
          return b.price - a.price
        case 'newest':
          return new Date(b.createdAt) - new Date(a.createdAt)
        default: // name
          return a.name.localeCompare(b.name)
      }
    })

    setFilteredProducts(result)
    setCurrentPage(1) // Reset to first page when filters change
  }, [searchTerm, selectedBrand, sortBy, priceRange, inStockOnly, productsInCategory])

  // Pagination logic
  const indexOfLastProduct = currentPage * productsPerPage
  const indexOfFirstProduct = indexOfLastProduct - productsPerPage
  const currentProducts = filteredProducts.slice(indexOfFirstProduct, indexOfLastProduct)
  const totalPages = Math.ceil(filteredProducts.length / productsPerPage)

  const paginate = (pageNumber) => setCurrentPage(pageNumber)

  // Format price to currency
  const formatPrice = (price) => {
    return new Intl.NumberFormat('en-US', {
      style: 'currency',
      currency: 'MWK'
    }).format(price / 100)
  }

  const handlePriceChange = (e, type) => {
    const value = parseInt(e.target.value) || 0
    setPriceRange(prev => ({ ...prev, [type]: value }))
  }

  return (
    <div className="container mx-auto px-4 py-8">
      {/* Header */}
      <div className="mb-8">
        <h1 className="text-3xl font-bold">{category.name || "Products"}</h1>
        <p className="text-gray-600">{filteredProducts.length} products found</p>
      </div>

      {/* Search and Filter Bar */}
      <div className="bg-gray-100 p-4 mb-8 rounded-lg">
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-4">
          {/* Search */}
          <div>
            <label className="block text-sm font-medium mb-1">Search</label>
            <input
              type="text"
              value={searchTerm}
              onChange={(e) => setSearchTerm(e.target.value)}
              placeholder="Search products..."
              className="w-full p-2 border rounded"
            />
          </div>

          {/* Brand Filter */}
          <div>
            <label className="block text-sm font-medium mb-1">Brand</label>
            <select
              value={selectedBrand}
              onChange={(e) => setSelectedBrand(e.target.value)}
              className="w-full p-2 border rounded"
            >
              <option value="">All Brands</option>
              {brands.map(brand => (
                <option key={brand._id} value={brand._id}>{brand.name}</option>
              ))}
            </select>
          </div>

          {/* Sort By */}
          <div>
            <label className="block text-sm font-medium mb-1">Sort By</label>
            <select
              value={sortBy}
              onChange={(e) => setSortBy(e.target.value)}
              className="w-full p-2 border rounded"
            >
              <option value="name">Name</option>
              <option value="price-asc">Price: Low to High</option>
              <option value="price-desc">Price: High to Low</option>
              <option value="newest">Newest First</option>
            </select>
          </div>

          {/* In Stock Only */}
          <div className="flex items-end">
            <label className="flex items-center">
              <input
                type="checkbox"
                checked={inStockOnly}
                onChange={(e) => setInStockOnly(e.target.checked)}
                className="mr-2"
              />
              <span>In Stock Only</span>
            </label>
          </div>
        </div>

        {/* Price Range */}
        <div className="mt-4">
          <label className="block text-sm font-medium mb-1">
            Price Range: {formatPrice(priceRange.min)} - {formatPrice(priceRange.max)}
          </label>
          <div className="flex gap-4">
            <input
              type="number"
              min="0"
              max={priceRange.max}
              value={priceRange.min}
              onChange={(e) => handlePriceChange(e, 'min')}
              className="w-full p-2 border rounded"
              placeholder="Min Price"
            />
            <input
              type="number"
              min={priceRange.min}
              value={priceRange.max}
              onChange={(e) => handlePriceChange(e, 'max')}
              className="w-full p-2 border rounded"
              placeholder="Max Price"
            />
          </div>
        </div>
      </div>

      {/* Products Grid */}
      {currentProducts.length > 0 ? (
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
          {currentProducts.map(product => (
            <div key={product._id} className="border rounded-lg overflow-hidden shadow-sm hover:shadow-md transition-shadow">
              <div className="relative h-32 w-full">
  <img 
    src={product.photo || '/placeholder.jpg'} 
    alt={product.name}
    
    style={{ 
        height: '220px', 
        objectFit: 'cover',
        objectPosition: 'center'
      }}
  />
</div>
              <div className="p-4">
                <h3 className="font-bold text-lg mb-1">{product.name}</h3>
                <div className="flex justify-between items-center mb-2">
                  <span className="font-semibold text-lg">{formatPrice(product.price)}</span>
                  <span className={`text-sm px-2 py-1 rounded ${product.inStock ? 'bg-green-100 text-green-800' : 'bg-red-100 text-red-800'}`}>
                    {product.inStock ? 'In Stock' : 'Out of Stock'}
                  </span>
                </div>
                <p className="text-gray-600 text-sm mb-4 line-clamp-2">{product.description}</p>
                <Link href={`/product/${product._id}`} className="block w-full text-center bg-blue-600 text-white py-2 rounded hover:bg-blue-700 transition-colors">
                  View Details
                </Link>
              </div>
            </div>
          ))}
        </div>
      ) : (
        <div className="text-center py-12">
          <h3 className="text-xl font-medium">No products found</h3>
          <p className="text-gray-600 mt-2">Try adjusting your filters or search term</p>
        </div>
      )}

      {/* Pagination */}
      {totalPages > 1 && (
        <div className="flex justify-center mt-8">
          <nav className="flex items-center">
            <button 
              onClick={() => setCurrentPage(prev => Math.max(prev - 1, 1))}
              disabled={currentPage === 1}
              className="px-3 py-1 border rounded-l disabled:opacity-50"
            >
              Previous
            </button>
            
            <div className="flex">
              {[...Array(totalPages)].map((_, i) => (
                <button
                  key={i}
                  onClick={() => paginate(i + 1)}
                  className={`px-3 py-1 border-t border-b ${
                    currentPage === i + 1 ? 'bg-blue-600 text-white' : ''
                  }`}
                >
                  {i + 1}
                </button>
              ))}
            </div>
            
            <button
              onClick={() => setCurrentPage(prev => Math.min(prev + 1, totalPages))}
              disabled={currentPage === totalPages}
              className="px-3 py-1 border rounded-r disabled:opacity-50"
            >
              Next
            </button>
          </nav>
        </div>
      )}
    </div>
  )
}